import React, { useState, useMemo, useEffect, useRef } from "react";
//import BackBar from "../../components/BackBar";
import AppBar from "../../components/AppBar";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Button from "../../components/Button";
import VideoMap from "../../components/Video/VideoMap";
import AdBanner from "../../components/AdBanner";
import VideoToolbar from "../../components/Video/VideoToolbar";
import { VideoList, VideoListDetail, Product } from "../../type/videoType";
import { ComicListData, Product as ComicProduct } from "../../type/comicType";
import CateDropdown from "../../components/AppBar/CateDropdown";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "../../store";
import {
  fetchVideoList,
  getSearchHot,
  setSearchComicData,
  setSearchKw,
  setSearchVideoData,
  search,
  setSearchCate,
} from "../../store/videoSlice";
import ComicMap from "../../components/Comic/ComicMap";
import { fetchComicList } from "../../store/comicSlice";
import Loading from "../../components/Loading";

function Search() {
  const dispatch: AppDispatch = useDispatch();
  const { t } = useTranslation();
  const location = useLocation();
  const { kw, cate } = location.state || { kw: "", cate: "" };

  const searchComicData = useSelector(
    (state: RootState) => state.video.searchComicData
  );
  const searchVideoData = useSelector(
    (state: RootState) => state.video.searchVideoData
  );
  const searchHot = useSelector((state: RootState) => state.video.searchHot);
  const searchCate = useSelector((state: RootState) => state.video.searchCate);
  const searchKw = useSelector((state: RootState) => state.video.searchkw);
  const comicList = useSelector(
    (state: RootState) => state.comic.comicListData
  );
  const videoList = useSelector(
    (state: RootState) => state.video.videoList?.["日本"]
  );

  let init: string = "";

  const [haveData, setHaveData] = useState(true);
  const [title, setTitle] = useState("");
  const [allVideo, setAllVideo] = useState<Product[]>([]);
  const [allComic, setAllComic] = useState<ComicProduct[]>([]);
  const [allSearchVideo, setAllSearchVideo] = useState<Product[]>([]);
  const [allSearchComic, setAllSearchComic] = useState<ComicProduct[]>([]);
  const [loading, setLoading] = useState(true);

  const handleGetMoreVideo = () => {
    dispatch(
      fetchVideoList({
        name: "日本",
        url: "?cat=jp",
        type: "mv",
        num: Number(videoList?.pageNum) + 1
      })
    );
  }

  const handleGetMoreComic = () => {
    if (comicList) {
      dispatch(fetchComicList(
        { 
          url: `num=${ Number(comicList.pageNum) + 1 }` 
        }
      ));
    }
  }

  const handleGetMoreSearchVideo = () => {
    if (searchKw) {
      dispatch(
        search({
          kw: searchKw,
          cat: searchCate ?? '',
          num: Number(searchVideoData?.pageNum) + 1
        })
      );
    }
  }

  const handleGetMoreSearchComic = () => {
    if (searchKw) {
      dispatch(
        search({
          kw: searchKw,
          cat: 'amine',
          num: Number(searchComicData?.pageNum) + 1
        })
      );
    }
  }

  const updateAllSearchComic = () => {
    if (
        searchComicData 
      && Number(searchComicData.pageNum) !== 1
    ) {
      setAllSearchComic((prevAllSearchComic) => [...prevAllSearchComic, ...searchComicData.products]);
    }
  }

  const updateAllComic = () => {
    if (
        comicList 
      && Number(comicList.pageNum) !== 1
    ) {
      setAllComic((prevAllComic) => [...prevAllComic, ...comicList.products]);
    }
  }

  const updateAllSearchVideo = () => {
    if (
      searchVideoData 
      && Number(searchVideoData.productTotal) !== 0
      && Number(searchVideoData.pageNum) !== 1
    ) {
      setAllSearchVideo((prevAllSearchVideo) => [...prevAllSearchVideo, ...searchVideoData.products]);
    }
  }

  const updateAllVideo = () => {
    if (
        videoList 
      && Number(videoList.pageNum) !== 1
    ) {
      setAllVideo((prevAllVideo) => [...prevAllVideo, ...videoList.products]);
    }
  }

  const handleInitAllSearchComic = () => {
    if (
        searchComicData 
      && Number(searchComicData.pageNum) === 1
    ) {
      setAllSearchComic([]);
      setAllSearchComic(searchComicData.products);
    }
  };

  const handleInitAllComic = () => {
    if (
        comicList 
      && Number(comicList.pageNum) === 1
    ) {
      setAllComic([]);
      setAllComic(comicList.products);
    }
  };

  const handleInitAllVideo = () => {
    if (
        videoList 
      && Number(videoList.pageNum) === 1
    ) {
      setAllVideo([]);
      setAllVideo(videoList.products);
    }
  };

  const handleInitAllSearchVideo = () => {
    if (
      searchVideoData 
      && Number(searchVideoData.pageNum) === 1
    ) {
      setAllSearchVideo([]);
      setAllSearchVideo(searchVideoData.products);
    }
  };

  useEffect(() => {
    handleInitAllSearchComic();
    updateAllSearchComic();

    handleInitAllComic();
    updateAllComic();

    handleInitAllVideo();
    updateAllVideo();

    handleInitAllSearchVideo();
    updateAllSearchVideo();

  }, [videoList, searchVideoData, comicList, searchComicData])

  useEffect(() => {
    dispatch(setSearchCate(''));
    dispatch(setSearchKw(kw));
    dispatch(fetchComicList(''));
    dispatch(getSearchHot());
    dispatch(
      fetchVideoList({
        name: "日本",
        url: "?cat=jp",
        type: "mv",
      })
    );
  }, [dispatch]);

  useEffect(() => {
    if (haveData) {
      setTitle("");
    } else {
      setTitle("猜你喜歡");
    }
  }, [haveData]);

  useEffect(() => {
    if (
      searchCate === "amine" &&
      searchComicData &&
      searchComicData.productTotal !== 0
    ) {
      setHaveData(true);
    } else if (
      searchCate !== "amine" &&
      searchVideoData &&
      searchVideoData.productTotal !== 0
    ) {
      setHaveData(true);
    } else {
      setHaveData(false);
    }
  }, [searchVideoData, searchComicData]);

  useEffect(() => {
    setAllSearchVideo([]);
    dispatch(setSearchKw(''));
    dispatch(setSearchComicData(null));
    dispatch(setSearchVideoData(null));
  }, [searchCate]);

  useEffect(() => {
    console.log(kw);
    console.log(cate);
    if(!(kw === '' && cate === '')) {
      console.log('kw:', kw);
      console.log('cate:', cate);
      dispatch(setSearchKw(kw));
      dispatch(setSearchCate(cate));
      dispatch(
        search({
          kw: kw,
          cat: cate ?? ''
        })
      );
      
    }
  }, [kw, cate]);

  useEffect(() => {
      setTimeout(() => {
        window.scrollTo(0, 1);
        window.scrollTo(0, 0);
      }, 100);
      // 強制觸發瀏覽器 repaint，避免初次畫面為空白
      setTimeout(() => {
        setLoading(false);
      }, 500);
    }, []);

  const handleSetValue = (value: string) => {
    dispatch(setSearchKw(value));
    dispatch(
      search({
        kw: value,
        cat: searchCate ?? "",
      })
    );
  };

  return (
    <div>
      {loading ? (
        <Loading/>
      ) : (
        <div>
          <AppBar type={"search"} home />
          {!haveData && (
            <div style={{ marginTop: "8%" }}>
              {((searchCate !== "amine" && searchVideoData) ||
                (searchCate === "amine" && searchComicData)) && (
                <div className="p-4  text-center">
                  <h2 className="text-xl font-bold">{t('沒有找到商品')}</h2>
                  <p>{t('試試搜尋不同關鍵字')}</p>
                </div>
              )}
              <div className="flex px-4 items-end justify-between">
                <h2 className="text-xl font-bold">{t('熱門關鍵字')}</h2>
              </div>
              <div className="px-4 py-2 flex flex-wrap gap-y-2 gap-x-4">
                {searchHot?.map((option) => (
                  <Button
                    key={option.sh_comic}
                    className="button rounded-full active"
                    onClick={() => handleSetValue(option.sh_name)}
                  >
                    {t(option.sh_name)}
                  </Button>
                ))}
              </div>
              <VideoToolbar title={t(title)} />
              {searchCate === "amine" ? (
                <ComicMap data={allComic || []} onReachBottom={handleGetMoreComic} lastPage={Number(comicList?.pageNum) === Number(comicList?.pageTotals)}/>
              ) : (
                <VideoMap data={allVideo || []} onReachBottom={handleGetMoreVideo} lastPage={Number(videoList?.pageNum) === Number(videoList?.pageTotals)}/>
              )}
            </div>
          )}
          {searchCate === "amine" ? (
            <ComicMap
              title={t(`${title}`)}
              data={allSearchComic || []}
              onReachBottom={handleGetMoreSearchComic}
              lastPage={Number(searchComicData?.pageNum) === Number(searchComicData?.pageTotals)}
            />
          ) : (
            <VideoMap
              title={t(`${title}`)}
              data={allSearchVideo || []}
              onReachBottom={handleGetMoreSearchVideo}
              lastPage={Number(searchVideoData?.pageNum) === Number(searchVideoData?.pageTotals)}
              isSearch
            />
          )}
        <AdBanner />
        </div>
      )}
    </div>
  );
}

export default Search;
